.welcome-screen {
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  background-color: white;
  color: green;

  h1 {
    padding-top: 2rem;
  }
}

.login-button {
  color: green;
  text-transform: uppercase;
  text-decoration: none;
  background: #ffffff;
  padding: 20px;
  border: 4px solid #494949;
  border-radius: 12px;
  display: inline-block;
  transition: all 0.4s ease 0s;
  min-width: 10rem;
  margin: 1rem;
}

.google {
  &-btn {
    width: 184px;
    height: 42px;
    margin: 1rem;
    background-color: #1dc90e;
    border-radius: 12px;

    &:hover {
      box-shadow: 0 0 6px #1dc90e;
    }
    &:active {
      background: #1dc90e;
    }

    &__text {
      float: right;
      color: #fff;
      text-shadow: 1px 1px 2px black, -1px -1px 1px black;
      font-size: 14px;
      letter-spacing: 0.2px;
      font-family: "Roboto";
      background-color: #1dc90e;
      width: 80%;
      height: 100%;
      text-align: center;
      border-top-right-radius: 12px;
      border-bottom-right-radius: 12px;
      cursor: pointer;
    }
  }

  &-icon {
    position: absolute;
    margin-top: 11px;
    margin-left: -8px;
    width: 18px;
    height: 18px;

    &-wrapper {
      position: absolute;
      margin-top: 1px;
      margin-left: 1px;
      width: 40px;
      height: 40px;
      border-top-left-radius: 12px;
      border-bottom-left-radius: 12px;
      background-color: #fff;
      cursor: pointer;
    }
  }
}
